.Polaris-MediaCard {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__hero-image {
    width: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.Polaris-MediaCard__Popover {
  display: none;
}

.Polaris {
  &-Heading {
    color: rgb(145, 179, 200);
    padding: 5rem 1rem 1.5rem;
    text-align: center;
  }
  &-Stack__Item {
    font-size: 1.5rem;
    padding: 0 1rem;
  }
  &-Button {
    flex-basis: 100%;
    margin: 1.5rem 0;
    align-items: center;
    // background-color: #fee6e3;
    background-color: rgb(164, 189, 204);
    border: 2px solid #111;
    border-radius: 8px;
    box-sizing: border-box;
    color: #111;
    cursor: pointer;
    display: flex;
    font-family: Inter, sans-serif;
    font-size: 16px;
    height: 48px;
    justify-content: center;
    line-height: 24px;
    max-width: 100%;
    padding: 0 25px;
    position: relative;
    text-align: center;
    text-decoration: none;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;

    &:after {
      background-color: #111;
      border-radius: 8px;
      content: "";
      display: block;
      height: 48px;
      left: 0;
      width: 100%;
      position: absolute;
      top: -2px;
      transform: translate(8px, 8px);
      transition: transform 0.2s ease-out;
      z-index: -1;
    }

    &:hover:after {
      transform: translate(0, 0);
    }

    &:active {
      background-color: #ffdeda;
      outline: 0;
    }

    &:hover {
      outline: 0;
    }

    @media (min-width: 768px) {
      & {
        padding: 0 40px;
      }
    }
  }
}

